/* Set up a 16px base font size and use a sans-serif font */
html {
  font-size: 16px;
  font-family: sans-serif;
}

/* Set a dark color scheme with gray, black, and green accents */
body {
  background-color: #333333;
  color: #f0f0f0;
}

a {
  color: #00ff00;
}

h1, h2, h3, h4, h5, h6 {
  color: #ffffff;
}

/* Style the header and navigation bar */
header {
  padding: 0px;
  background-color: #444444;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;
}

nav {
  background-color: #444444;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  height: 50px;
}

nav ul {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
  gap: 1rem;
}

nav li {
  margin: 0 5px;
}

nav a{
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: .25rem;
}

nav li.active{
  background-color: #555;
}

nav li:hover{
  background-color: #777;
}

/* Style the main content area */
main {
  margin-top: 80px;
  padding: 40px;
}

/* Style the section headings */
h2 {
  font-size: 2em;
  margin-bottom: 20px;
}

/* Style the footer */
footer {
  padding: 20px;
  background-color: #444444;
  color: #ffffff;
  text-align: center;
  position: absolute;
  bottom: 0;
  width: 100%;
}
